.technologies {
    background: url('/public/images/brilliant.png');
    background-color: var(--darkbg);
    padding-bottom: 20px;
    margin-top: 100px;
    margin-bottom: 100px;
    width: device-width;
  }

.tech-container {
    padding-left: 15%;
    padding-right: 15%;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
}

.tech {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
    padding: 0px 10px;
}

.section-span-technologies {
    float: right;
    margin-right: 20%;
    display: block;
    width: 72px;
    height: 5px;
    background: linear-gradient(90deg, #f87537 0%, #fba81f 100%);
    border-radius: 5px;
}

.section-header-technologies {
    color: var(--darktext);
    padding-top: 0.75%;
    font-size: 40px;
    font-weight: 400;
    margin: 0;
}

.section-header-right {
    text-align: right;
    align-self: flex-end;
    margin-right: 20%;
}

@media screen and (max-width: 1200px) {
    .section-header-technologies {
        margin: 20px;
    }

    .tech-img {
        max-width: 8em;
    }
}

@media screen and (max-width: 800px) {

    /* .tech-container {
        flex-direction: column;
        align-items: center;
    } */

    .tech-img {
        max-width: 4.5em;
    }
}